import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class BookOpenProgramService {

  private bookOpenProgramMatDrawer = new BehaviorSubject<any>(null);
  bookopenprogramdrawercast = this.bookOpenProgramMatDrawer.asObservable();

  private bookOpenProgramFilterData = new BehaviorSubject<any>({});
  bookopenprogramfilterdatacast = this.bookOpenProgramFilterData.asObservable();

  private bookOpenProgramListData = new BehaviorSubject<any>(null);
  bookopenprogramdatalistcast = this.bookOpenProgramListData.asObservable();

  private bookOpenProgramListDataType = new BehaviorSubject<any>(null);
  bookopenprogramdatalisttypecast = this.bookOpenProgramListDataType.asObservable();

  private bookOpenProgramDataSubmitted = new BehaviorSubject<any>(false);
  bookopenprogramdatasubmittedcast = this.bookOpenProgramDataSubmitted.asObservable();

  constructor() { }

  getBookOpenProgramMatDrawer(){
    return this.bookOpenProgramMatDrawer.value
  }

  setBookOpenProgramMatDrawer(value){
    this.bookOpenProgramMatDrawer.next(value);
  }

  getBookOpenProgramFilterData(){
    return this.bookOpenProgramFilterData.value
  }

  setBookOpenProgramFilterData(value){
    this.bookOpenProgramFilterData.next(value);
  }

  getBookOpenProgramDataList(){
    return this.bookOpenProgramListData.value
  }

  setBookOpenProgramDataList(value){
    this.bookOpenProgramListData.next(value);
  }

  getBookOpenProgramDataListType(){
    return this.bookOpenProgramListDataType.value
  }

  setBookOpenProgramDataListType(value){
    this.bookOpenProgramListDataType.next(value);
  }

  getBookOpenProgramDataSubmitted(){
    return this.bookOpenProgramDataSubmitted.value
  }

  setBookOpenProgramDataSubmitted(value){
    this.bookOpenProgramDataSubmitted.next(value);
  }

}
