import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot, Resolve, Router, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs';

import { BookOpenProgramService } from 'src/app/modules/admin/open-program/book-open-program/book-open-program.service';
import { SharedService } from 'src/app/shared/services/shared.service';


@Injectable({
  providedIn: 'root'
})
export class BookOpenProgramResolver implements Resolve<boolean> {

  constructor(
    private router: Router,
    private bookOpenProgramService: BookOpenProgramService,
    private sharedService:SharedService
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
  {

    var data = this.bookOpenProgramService.getBookOpenProgramDataList();

    if(data == null){

      var parentUrl;

      if(state.url.indexOf('//') == -1){
        parentUrl = state.url.split('/').slice(0, -1).join('/');
      }
      else {
        parentUrl = state.url.split('//')[0].replace(/[{()}]/g, '')
      }

      // Navigate to parent url
      this.router.navigateByUrl(parentUrl);

    }


    return;


  }

}

@Injectable({
  providedIn: 'root'
})
export class BookOpenProgramDrawerResolver implements Resolve<boolean> {

  constructor(
    private router: Router,
    private bookOpenProgramService: BookOpenProgramService,
    private sharedService:SharedService
  ) { }

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any
  {

    var data = this.bookOpenProgramService.getBookOpenProgramMatDrawer();

    if(data == null){

      const parentUrl = state.url.split('/').slice(0, -1).join('/');

      // Navigate to parent url
      this.router.navigateByUrl(parentUrl);

    }


    return;


  }

}
