import { Component, Input, Output, OnInit, EventEmitter, ViewEncapsulation, ViewChild, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { BookOpenProgramService } from 'src/app/modules/admin/open-program/book-open-program/book-open-program.service';

import { ProgramService } from 'src/app/api/controllers/Program';
import { BatchService } from 'src/app/api/controllers/Batch';
import { ClassService } from 'src/app/api/controllers/Class';
import { StyleService } from 'src/app/api/controllers/Style';

import { SessionService } from 'src/app/core/session/session.service';

import { BookOpenProgramComponent } from 'src/app/modules/admin/open-program/book-open-program/book-open-program.component';



@Component({
  selector: 'search-open-book-program',
  templateUrl: './search-open-book-program.component.html',
  styleUrls: ['./search-open-book-program.component.scss']
})
export class SearchOpenBookProgramComponent implements OnInit {

  openProgramBatchList = [];
  openProgramBatchTypeList = [];
  openProgramLevelList = [];
  openProgramStyleList = [];
  openProgramPackageList = [];
  programList = [];

  program:any = {}

  isDrawer:boolean = false;

  @Output() public outputParams = new EventEmitter();

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private _changeDetectorRef: ChangeDetectorRef,
    private _bookOpenProgramComponent: BookOpenProgramComponent,
    private programService:ProgramService,
    private bookOpenProgramService: BookOpenProgramService,
    private batchService: BatchService,
    private classService: ClassService,
    private styleService: StyleService,
    private sessionService: SessionService
  ) { }

  goBack() {

    this._bookOpenProgramComponent.matDrawer.close();
    this.bookOpenProgramService.setBookOpenProgramMatDrawer(null)

    let type = this.bookOpenProgramService.getBookOpenProgramDataListType();
    this._router.navigateByUrl('/open-programs/book/list/'+type);

  }

  onOpenProgramBatchChange(event) {
    this.program.batchId = event[0].id
  }

  onOpenProgramTypeChange(event) {
    this.program.batchTypeId = event[0].id
  }

  onOpenProgramLevelChange(event) {
    this.program.levelId = event[0].id
  }

  onOpenProgramStyleChange(event) {
    this.program.styleId = event[0].id
  }

  onOpenProgramPackageChange(event) {
    this.program.packageId = event[0].id
  }

  onProgramChange(event) {
    this.program.programId = event[0].id
  }

  submitSearchOpenProgramForm(form) {

    if(this.isDrawer){
      this._bookOpenProgramComponent.matDrawer.close();
      this.bookOpenProgramService.setBookOpenProgramMatDrawer(null)
    }

    this._bookOpenProgramComponent.getSearchProgram(this.program)

  }

  clearFilter() {
    this.program = {};
    this._bookOpenProgramComponent.matDrawer.close();
    this.bookOpenProgramService.setBookOpenProgramMatDrawer(null)
    this._bookOpenProgramComponent.getSearchProgram(this.program)
  }

  ngOnInit(): void {

    //Open the drawer
    this.bookOpenProgramService.bookopenprogramdrawercast.subscribe(res => {

      if(res != null && res == this._activatedRoute.params['value'].id){
        this._bookOpenProgramComponent.matDrawer.open();
        this.isDrawer = true

        this.bookOpenProgramService.bookopenprogramfilterdatacast.subscribe((res:any) => {
          this.program = res
        })

      }
      else {
        this.isDrawer = false
      }

    });

    //main programs
    let prorgramListParams = {
      UserId: this.sessionService.userId,
      ClientId: this.sessionService.clientId
    }

    this.programService.getProgramList(prorgramListParams).subscribe((res:any) => {

      if(res.value) {
        this.programList = res.value;
      }

    })

    //batch types
    let batchTypeListParams = {
      UserId: this.sessionService.userId,
      ClientId: this.sessionService.clientId
    }

    this.batchService.getBatchCategoryList(batchTypeListParams).subscribe((res:any) => {

      if(res.value) {
        this.openProgramBatchTypeList = res.value;
      }

    });

    //class level
    let classLevelListParams = {
      UserId: this.sessionService.userId,
      ClientId: this.sessionService.clientId
    }

    this.classService.getClassLevelList(classLevelListParams).subscribe((res:any) => {
      if(res.value) {
        this.openProgramLevelList = res.value;
      }
    });

    //style
    let styleListParams = {
      UserId: this.sessionService.userId,
      ClientId: this.sessionService.clientId
    }

    this.styleService.getStyleList(styleListParams).subscribe((res:any) => {
      if(res.value) {
        this.openProgramStyleList = res.value;
      }
    });



  }

}
