import { Component, OnInit, ViewEncapsulation, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';

import { ProgramService } from 'src/app/api/controllers/Program';
import { BookOpenProgramService } from 'src/app/modules/admin/open-program/book-open-program/book-open-program.service';

import { BookOpenProgramComponent } from 'src/app/modules/admin/open-program/book-open-program/book-open-program.component';


@Component({
  selector: 'app-view-book-open-program-item',
  templateUrl: './view-book-open-program-item.component.html',
  styleUrls: ['./view-book-open-program-item.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class ViewBookOpenProgramItemComponent implements OnInit {

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private _changeDetectorRef: ChangeDetectorRef,
    private _bookOpenProgramComponent: BookOpenProgramComponent,
    private programService:ProgramService,
    private bookOpenProgramService: BookOpenProgramService,
  ) { }

  goBack() {
    this._bookOpenProgramComponent.matDrawer.close();
    this.bookOpenProgramService.setBookOpenProgramMatDrawer(null)
  }

  ngOnInit(): void {

    //Open the drawer
    this.bookOpenProgramService.bookopenprogramdrawercast.subscribe(res => {
      if(res != null && res == this._activatedRoute.params['value'].id){
        this._bookOpenProgramComponent.matDrawer.open();
      }
    });

  }

}
