import { Component, Input, Output, OnInit, ViewEncapsulation, ViewChild, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { Observable, BehaviorSubject, Subscription, Subject } from 'rxjs';
import { filter, map, startWith, debounceTime, takeUntil } from 'rxjs/operators';
import { FormControl } from '@angular/forms';

import { MatDrawer } from '@angular/material/sidenav';

import { ProgramService } from 'src/app/api/controllers/Program';
import { BatchService } from 'src/app/api/controllers/Batch';
import { BookOpenProgramService } from 'src/app/modules/admin/open-program/book-open-program/book-open-program.service';

import { BookOpenProgramComponent } from 'src/app/modules/admin/open-program/book-open-program/book-open-program.component';

import { SessionService } from 'src/app/core/session/session.service';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalsService } from 'src/app/modules/ui/modals/modals.service';

import * as moment from 'moment';


@Component({
  selector: 'app-book-open-program-list',
  templateUrl: './book-open-program-list.component.html',
  styleUrls: ['./book-open-program-list.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class BookOpenProgramListComponent implements OnInit {

  isProgramAvailable:boolean = false;
  isDataLoaded:boolean = true;

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';
  private _unsubscribeAll: Subject<any>;

  _programsList:any;
  fullProgramsList:any = [];

  searchData:FormControl;

  search:string = '';
  deleteSubscribe:Subscription;

  ItemStartIndex: any = 0;
  ItemEndIndex: any;
  totalItems: any = 0;
  itemLimit: any = 5;

  groupData: string = "";
  allData: any;

  programBatchList = []

  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private _bookOpenProgramComponent: BookOpenProgramComponent,
    private programService:ProgramService,
    private batchService: BatchService,
    private bookOpenProgramService: BookOpenProgramService,
    private sessionService: SessionService,
    private sharedService:SharedService,
    private modalsService: ModalsService
  ) {

    // Set the private defaults
    this._unsubscribeAll = new Subject();

    this._programsList = new BehaviorSubject(null);
    this.searchData = new FormControl('');

    // Register to the NavigationEnd event
    this._router.events
    .pipe(filter((event) => event instanceof NavigationEnd))
    .subscribe(() => {

      this.getProgramData();

    });

  }

  get programs$(): Observable<any>{
    return this._programsList.asObservable();
  }



  getDate(date) {
    return moment(date).format('Do MMM YYYY');
  }

  getProgramBatch(id) {
    var data = this.programBatchList.filter(item => {
      return item.id == id
    })

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
      return data[0].name;
    }
  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  viewOpenProgramData(id) {
    this._bookOpenProgramComponent.viewOpenProgramData(id);
  }

  getProgramData() {

    this.bookOpenProgramService.bookopenprogramdatalistcast.subscribe((res:any) => {

      if(res != null) {

        if(this._activatedRoute.params['value'].type == 'all') {

          this.fullProgramsList = res;
          this.allData = res;
          this._programsList.next(res);

          this.bookOpenProgramService.setBookOpenProgramDataListType('all')

        }
        else if(this._activatedRoute.params['value'].type == 'full') {

          let data = res.filter(item => {
            return item.type == 'Full'
          })

          this.fullProgramsList = data;
          this.allData = data;
          this._programsList.next(data);

          this.bookOpenProgramService.setBookOpenProgramDataListType('full')

        }
        else {

          let data = res.filter(item => {
            return item.type == 'Free'
          })

          this.fullProgramsList = data;
          this.allData = data;
          this._programsList.next(data);

          this.bookOpenProgramService.setBookOpenProgramDataListType('free')

        }

        this.programs$.pipe( filter(res => res != null),
        ).subscribe((res:any) => {

          this.totalItems = res.length;
          this.ItemStartIndex = 0;

          if (this.totalItems > this.itemLimit) {
            this.ItemEndIndex = this.itemLimit;
          }
          else {
            this.ItemEndIndex = this.totalItems;
          }

        })

      }

    })

  }

  ngOnInit(): void {

    this.getProgramData();

    let params = {
      UserId: this.sessionService.userId,
      ClientId: this.sessionService.clientId
    }

    this.batchService.getBatchCategoryList(params).subscribe((res:any) => {
      if(res.value)
      this.programBatchList = res.value;

    });

  }

}
