import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { BookOpenProgramComponent } from './book-open-program.component';
import { BookOpenProgramListComponent } from './book-open-program-list/book-open-program-list.component';
import { SearchOpenBookProgramComponent } from './search-open-book-program/search-open-book-program.component';
import { ViewBookOpenProgramItemComponent } from './view-book-open-program-item/view-book-open-program-item.component';

import { BookOpenProgramResolver } from './book-open-program.resolver';
import { BookOpenProgramDrawerResolver } from './book-open-program.resolver';


const routes: Routes = [
  { path: 'list', component: BookOpenProgramComponent,
    children: [
      { path: ":type", component: BookOpenProgramListComponent,
        resolve: {
          BookOpenProgramResolver
        }
      },
      {
        path: "filter/:id",
        component: SearchOpenBookProgramComponent,
        outlet: "drawer",
        resolve: {
          BookOpenProgramDrawerResolver
        }
      },
      {
        path: "view/:id",
        component: ViewBookOpenProgramItemComponent,
        outlet: "drawer",
        resolve: {
          BookOpenProgramDrawerResolver
        }
      }
    ]
  },
  { path: '', redirectTo: 'list', pathMatch: 'full' }
];
@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class BookOpenProgramRoutingModule { }
